import { BASE_URL, clientServer } from "@/config";
import DashboardLout from "@/layout/dashboarLayout/indexDash";
import UserLayout from "@/layout/userLayout";
import { useSearchParams } from "next/navigation";
import React, { useEffect, useState } from "react";
import style from "./style.module.css";
import { useRouter } from "next/router";
import { useDispatch, useSelector } from "react-redux";
import { getAllPost } from "@/config/redux/action/postAction";
import { getConnectionRequest } from "@/config/redux/action/authAction";
import { turborepoTraceAccess } from "next/dist/build/turborepo-access-trace";

const ServerSideRendring = ({ response }) => {
  const searchParams = useSearchParams();

  const [UserPost, setUserPost] = useState([]);
  const [Connection, setConnections] = useState(false);
  const [isConnectionNull ,setisConnectionNull] = useState(turborepoTraceAccess)

  const authState = useSelector((state) => state.auth);
  const postState = useSelector((state) => state.postReducer);
  const router = useRouter();
  const dispatch = useDispatch();

  const getPostUser = async () => {
    await dispatch(getAllPost());
    await dispatch(
      getConnectionRequest({ token: localStorage.getItem("token") })
    );
  };

  useEffect(() => {
    const post = postState.post.filter((post) => {
      return post.userId.username === router.query.username;
    });
    setUserPost(post);
  }, [postState.post]);

  useEffect(() => {
    if (
      authState.connections.some(
        (user) => user.connectionId._id === response.userId._id
      )
    )
      setConnections(true);
  }, [authState.connections]);

  useEffect(() => {
    getPostUser();
  });

  return (
    <UserLayout>
      <DashboardLout>
        <div className={style.contianer}>
          <div className={style.backDropContianer}>
            <img src={`${BASE_URL}/${response.userId.profilePicture}`} alt="" />
          </div>

          <div className={style.profileContianer_details}>
            <div style={{ display: "flex", gap: "0.7rem" }}>
              <div style={{ flex: "0.7" }}>
                <div
                  style={{
                    display: "flex",
                    width: "fit-content",
                    alignItems: "center",
                  }}
                >
                  <h4>{response.userId.name} </h4>
                  <p style={{ color: "gray" , marginLeft: " 0.5rem" }}>@{response.userId.username} </p>
                </div>

                {Connection ? (
                  <button className={style.connectedBtn}>Connected</button>
                ) : (
                  <button
                    onClick={async () => {
                      await dispatch(
                        sendConnectionRequest({
                          token: localStorage.getItem("token"),
                          user_id,
                        })
                      );
                    }}

                    className={style.connectBtn}
                  >
                    Connect
                  </button>
                )}

                <div>
                  <p>{response.bio} </p>
                </div>
              </div>

              <div style={{ flex: "0.3" }}>
                <h4> Recent Activity</h4>
                {UserPost.map((post) => {
                  return (
                    <div key={post._id} className={style.postCard}>
                      <div className={style.card}>
                        <div className={style.card_ProfileContainer}>
                          {post.media !== "" ? (
                            <img src={`${BASE_URL}/${post.media}`} alt="" />
                          ) : (
                            <div style={{width: "3.4rem" , height: " 3.4rem"}}></div>
                          )}
                        </div>
                        <p>{post.body} </p>
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        </div>
      </DashboardLout>
    </UserLayout>
  );
};

export default ServerSideRendring;

export const getServerSideProps = async (context) => {
  try {
    const request = await clientServer.get("/getUserProfileByUsername", {
      params: {
        username: context.query.username,
      },
    });
    const response = await request.data;
    return { props: { response: request.data.profile } };
  } catch (err) {
    return { props: { response: null } };
  }
};
